.nav {
  width: 100%;
  display: flex;
  flex-direction: column;
}

.list {
  width: 100%;
  height: 2em;
  display: flex;
  justify-content: center;
  align-items: center;
  margin-bottom: 0.3rem;
  cursor: pointer;
}

.list:hover {
  background-color: rgb(4, 56, 54);
}

.list span {
  flex: 0.5;
  height: 100%;
  text-transform: capitalize;
}
